.rt-controls {
  display: none;
}

.rt-track-key__toggle {
  display: none;
}

.rt-track-keys > .rt-track-key > .rt-track-key__entry {
  text-align: right;
  padding-right: 20px;
}

.rt-marker--now {
  margin: 0;
  z-index: 1;
  width: 100%;
  transform: translateX(calc(-100% + 10px));
  border-right-width: 10px;
  border-right-style: solid;
  border-right-color: rgba(255, 0, 127, 0.5);
  background-color: rgba(128, 128, 128, 0.2);
}

.rt-marker__label {
  display: none;
}

.rt-track__element {
  z-index: 1;
}

.rt-track-key__entry {
  line-height: 1;
}

.rt-track-key,
.rt-track-key__entry,
.rt-track__elements {
  height: 40px;
}

.rt-track__element {
  top: 6px;
}

.rt-element {
  height: 26px;
  line-height: 30px;
}
