body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.rt-controls {
  display: none;
}

.rt-track-key__toggle {
  display: none;
}

.rt-track-keys > .rt-track-key > .rt-track-key__entry {
  text-align: right;
  padding-right: 20px;
}

.rt-marker--now {
  margin: 0;
  z-index: 1;
  width: 100%;
  -webkit-transform: translateX(calc(-100% + 10px));
          transform: translateX(calc(-100% + 10px));
  border-right-width: 10px;
  border-right-style: solid;
  border-right-color: rgba(255, 0, 127, 0.5);
  background-color: rgba(128, 128, 128, 0.2);
}

.rt-marker__label {
  display: none;
}

.rt-track__element {
  z-index: 1;
}

.rt-track-key__entry {
  line-height: 1;
}

.rt-track-key,
.rt-track-key__entry,
.rt-track__elements {
  height: 40px;
}

.rt-track__element {
  top: 6px;
}

.rt-element {
  height: 26px;
  line-height: 30px;
}

.vehicle-marker {
  transition: top 1000ms linear, left 1000ms linear;
}

